'use client';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import axios from '@/lib/axios';
import { getAccessToken, getUser } from '@/stores/userStore';

export function useRedirectIfAuth(redirectTo = '/dashboard/main') {
  const router = useRouter();

  useEffect(() => {
    // Check for existing valid token first
    const token = getAccessToken();
    if (token) {
      // Quick token validation - check if it's not expired
      try {
        const payload = JSON.parse(atob(token.split('.')[1]));
        const isExpired = payload.exp * 1000 < Date.now();
        if (!isExpired) {
          router.replace(redirectTo);
          return;
        }
      } catch (e) {
        // Invalid token, remove it
        localStorage.removeItem('accessToken');
      }
    }

    // Only attempt refresh if we have clear indication of existing session
    const hasRefreshCookie = typeof window !== 'undefined' && 
      document.cookie.includes('refreshToken=');
    
    if (!hasRefreshCookie) return; // No refresh token, don't attempt

    // Attempt refresh only once
    (async () => {
      try {
        const resp = await axios.post('/auth/refresh');
        const newToken = resp.data?.accessToken;
        if (newToken) {
          localStorage.setItem('accessToken', newToken);
          router.replace(redirectTo);
        }
      } catch (err) {
        // Clean up stale data
        localStorage.removeItem('user');
        localStorage.removeItem('accessToken');
        console.debug('Refresh failed, cleaned up stale session');
      }
    })();
  }, []); // Remove dependencies to prevent re-runs
}